import { default as Lang } from '../../const/lang'
import { langs } from './speech'

// : https://so-zou.jp/web-app/tech/data/code/language.htm
export function getName(lang) {
  switch (lang) {
    case Lang.JA_JP:
      return ''
    case Lang.EN_US:
      return '(US)'
    case Lang.DE_DE:
      return ''
    case Lang.EN_GB:
      return '(GB)'
    case Lang.ES_ES:
      return ''
    case Lang.ES_US:
      return '(US)'
    case Lang.FR_FR:
      return ''
    case Lang.HI_IN:
      return ''
    case Lang.ID_ID:
      return ''
    case Lang.IT_IT:
      return ''
    case Lang.KO_KR:
      return ''
    case Lang.NL_NL:
      return ''
    case Lang.PL_PL:
      return ''
    case Lang.PT_BR:
      return ''
    case Lang.RU_RU:
      return ''
    case Lang.ZH_CN:
      return '(CN)'
    case Lang.ZH_HK:
      return '(HK)'
    case Lang.ZH_TW:
      return '(TW)'
    default:
      console.error('Not Found')
  }
}

export function getList() {
  return Object.values(Lang).map(value => ({
    value,
    name: getName(value),
  }))
}

export function getUsableList() {
  return getList().filter(item => langs.includes(item.value))
}

export function getSelectableList(value) {
  const list = getUsableList()
  const isExists = list.some(item => item.value === value)

  if (isExists) return list

  list.push({
    value,
    name: getName(value),
    color: 'rgba(0, 0, 0, 0.3)',
  })

  return list
}
